</template>

<script>
export default {
    name: 'DashboardCoreFooter',

    data: () => ({
        links: [
            {
                href: '#',
                text: 'About Us'
            },
            {
                href: '#',
                text: 'Blog'
            },
            {
                href: '#',
                text: 'Licenses'
            }
        ]
    }),
    computed: {
        teamEnvUrlAndSubLink() {
            const baseUrl = 'https://pendo-perfserf.appspot.com/'; // add you baseUrl here ex. 'https://app.pendo-perfserf.pendo-dev.com/'
            const subId = '5716561121771520'; // add your subId here ex. '5634161670881280'
            const base64EncodedSearchParams = window.btoa(
                `?after=&subId=${subId}`
            );
            return `${baseUrl}/deep-links/${base64EncodedSearchParams}`;
        },
        teamEnvUrlAndSub() {
            const teamEnv = 'perfserf'; // add the name of your team env ex. 'perf-serfs'
            const subName = 'yrollam'; // add your subName ex. 'pendo-internal'
            return `Pendo ${teamEnv} Sub: ${subName}`;
        }
    }
};
</script>

